import {useState} from 'react'
import axios from 'axios'
import { isDisabled } from '@testing-library/user-event/dist/utils'

export default function Todos(){
    
    if(localStorage.getItem('token') === "undefined"){
        localStorage.removeItem('token')
    }

     //Todo  url (createTodo/getTodo/updateTodo/deleteTodo)
     let url = "https://pre-onboarding-selection-task.shop/todos"  
  
    //   input    
    const [todo, setTodo] = useState("")
    
    // getTodo  
    const [datas, setGetDatas] = useState([])

    //   input   
    function listenTodo(e){
        setTodo(e.target.value)
    }

    //   token 
    const token = localStorage.getItem('token')
    console.log(token)
    
    //Todo  url (createTodo/getTodo/updateTodo/deleteTodo)
   // let url = "https://pre-onboarding-selection-task.shop/todos"  


    //Todo 
    function createTodo(){

        let data = {
                     "todo": todo
                   }
                   
                   axios
                   .post(url,  JSON.stringify(data), {
                   headers: {
                               "Content-Type": `application/json`,
                               "Authorization" : `Bearer ${token}`,
                               "withCredentials" : true
                            },
                   })
                   .then((res) => {
                       localStorage.setItem('token', res.data.access_token)
                   });  
        }     
    
        // Todo  

        function getTodo(){
                
                axios
                .get(url, {
                        headers: {
                                   "Authorization" : `Bearer ${token}`
                                },
                    })
                .then((res) => {
                           setGetDatas(res.data)
                           console.log(datas) 
                    })
                .catch((error)=>{
                            console.log(error.response)
                    });  
            }    
       
        // Todo     

        function deleteTodo(id){
                       
                console.log(id)
                       
                axios      
                .delete(url + `/${id}`, {
                       headers: {
                                   "Authorization" : `Bearer ${token}`
                                },
                       })
                .then((res) => {
                           setGetDatas(res.data)
                       })
                .catch((error)=>{
                            console.log(error.response)
                       });  
            } 

        // Todo     

        function updateTodo(id){
                       
                console.log(id)
                
                axios
               .put(url + `/${id}`, {
                    headers: {
                                "Authorization" : `Bearer ${token}`
                             },
                    })
               .then((res) => {
                   setGetDatas(res.data)
                    })
               .catch((error)=>{
                    console.log(error.response)
                });  
    }  

    const tdClick =(e) => {
        console.log(e.target)
    }

    return(
        <div className ="todoList">
            <h1>TodoList</h1>
            
            <div class="headerTodoList">
                <div class="getTodo">
                    <button onClick={getTodo}></button>
                </div>
                <div class="inputTodo">
                    <p> </p>
                    <input onChange={listenTodo} type="text" placeholder='input Todo'/>
                    <button onClick={createTodo}></button>
                </div>
            </div>
            <div id ="todotable" className = "todoTable">
                <table> 
                    <thead>
                    <tr>
                        <td><strong>ID</strong></td>
                        <td><strong>Todo</strong></td>    
                        <td><strong></strong></td>
                        <td><strong></strong></td>
                        <td><strong></strong></td>
                        <td><strong></strong></td>
                        <td><strong></strong></td>
                        <td><strong></strong></td>
                    </tr>
                    </thead>
                    <tbody>
                    {datas.map((data) => (
                        <tr onClick = {tdClick} key={data.id}>
                            <td>{data.id}</td>
                            <td>{data.todo}</td>
                            <td></td>
                            <td><button style={{width:"100%"}} onClick={updateTodo(data.id)}></button></td>
                            <td><input type="text" style={{width : "100px"}} disabled ="true"/></td> {/* */}
                            <td><button style={{width:"100%"}}></button></td>
                            <td><button style={{width:"100%"}}></button></td>
                            <td><button style={{width:"100%"}} onClick={deleteTodo(data.id)}></button></td>
                        </tr>
                    ))}
                    </tbody>
                </table>
            </div>
            
        </div>
        
    )
}